import api from "./api";
import Message from "@/plugins/message";

/**
 * 
 * @author mutoe <mutoe@foxmail.com>
 * @export
 * @returns
 */
export function getCurrencyInfo() {
  return api.get("/currency", { validateStatus: s => s === 200 });
}

/**
 * 
 * @author mutoe <mutoe@foxmail.com>
 * @export
 * @param {Object} params
 * @param {number} [params.limit]
 * @param {number} [params.after]
 * @param {string} [params.action] -  [recharge: , cash: ] 
 * @param {number} [params.type]  [1: , -1: ] 
 * @returns
 */
export function getCurrencyOrders(params) {
  return api.get("/currency/orders", {
    params,
    validateStatus: s => s === 200
  });
}

/**
 * 
 * @author mutoe <mutoe@foxmail.com>
 * @export
 * @param {Object} data
 * @param {string} data.type 
 * @param {number} data.amount (RMB)
 * @param {number} data.from  h52
 * @returns
 */
export function postCurrencyRecharge(data) {
  const url = "/currencyRecharge/orders";
  return api.post(
    url,
    { ...data, from: 2 },
    { validateStatus: s => s === 201 }
  );
}

/**
 * 
 * @author mutoe <mutoe@foxmail.com>
 * @export
 * @param {Object} data
 * @param {number} data.amount
 * @returns
 */
export function postCurrencyWithdraw(data) {
  return api
    .post("/currency/cash", data, {
      validateStatus: s => s === 201
    })
    .catch(({ response }) => {
      Message.error(response.data);
    });
}
