import wx from "weixin-js-sdk";
import api from "@/api/api.js";
import Message from "@/plugins/message-box";

/**
 *   
 * @author jsonleex <jsonlseex@163.com>
 */
const __JsApiList = [
  "checkJsApi",
  "onMenuShareQQ",
  "onMenuShareWeibo",
  "onMenuShareTimeline",
  "onMenuShareAppMessage"
];

export default (url, options) => {
  if (!url) throw new Error(": URL");
  api.post(`socialite/wxconfig`, { url }).then(
    ({ data: { timestamp, signature, appid, noncestr } }) => {
      const wxconfig = {
        debug: false,
        timestamp,
        signature,
        appId: appid,
        nonceStr: noncestr,
        jsApiList: __JsApiList
      };
      const shareObj = {
        imgUrl: "", // 
        desc: "ThinkSNS+", // 
        title: "ThinkSNS+", // 
        type: "link", //  [music, video, link]
        dataUrl: "", // typemusicvideo,
        link: window.location.href, // JS ,
        /**
         * 
         * @author jsonleex <jsonlseex@163.com>
         */
        success: () => {
          Message.success("");
        },
        /**
         * 
         * @author jsonleex <jsonlseex@163.com>
         */
        cancel: () => {
          Message.info("");
        }
      };

      options = Object.assign(shareObj, options);

      wx.config(wxconfig);
      wx.ready(() => {
        wx.onMenuShareQQ(options);
        wx.onMenuShareWeibo(options);
        wx.onMenuShareTimeline(options);
        wx.onMenuShareAppMessage(options);
      });
      wx.error(res => {
        // eslint-disable-next-line
        console.warn(res);
      });
    },
    err => {
      // eslint-disable-next-line
      console.warn(err);
      throw new Error(": ");
    }
  );
};
