import axios from "axios";
import { baseURL } from "./index";
import router from "@/routers";
import Message from "@/plugins/message-box";
import lstore from "@/plugins/lstore/lstore.js";

let cancel;
let pending = {};

const instance = axios.create({
  baseURL,
  headers: {
    "X-Requested-With": "XMLHttpRequest"
  }
});

//
instance.interceptors.request.use(
  config => {
    //
    if (pending[config.url]) {
      pending[config.url]("");
      pending[config.url] = cancel;
    } else {
      pending[config.url] = cancel;
    }
    const TOKEN = lstore.getData("H5_ACCESS_TOKEN");
    if (TOKEN) {
      config.headers.Authorization = TOKEN;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

//
instance.interceptors.response.use(
  res => res,
  err => {
    const requireAuth = () => {
      setTimeout(() => {
        router.push({
          path: "/signin",
          query: { redirect: router.currentRoute.fullPath }
        });
      }, 500);
    };
    if (!axios.isCancel(err)) {
      if (err && err.response) {
        switch (err.response.status) {
          case 400:
            err.tips = "";
            break;
          case 401:
            err.tips = lstore.hasData("H5_CUR_USER")
              ? ""
              : "";
            lstore.clearData();
            requireAuth();
            break;
          case 403:
            err.tips = "";
            break;
          case 404:
            err.tips = ",";
            break;
          case 405:
            err.tips = "";
            break;
          case 408:
            err.tips = "";
            break;
          case 422: {
            const { data } = err.response;
            err.tips = data || { message: "" };
            break;
          }
          case 500:
            err.tips = "";
            break;
          case 501:
            err.tips = "";
            break;
          case 502:
            err.tips = "";
            break;
          case 503:
            err.tips = "";
            break;
          case 504:
            err.tips = "";
            break;
          case 505:
            err.tips = "http";
            break;
          default:
            err.tips = `${err.response.status}`;
        }
      } else {
        err.tips = "";
      }
      Message.error(err.tips);
    }
    return Promise.reject(err);
  }
);

export default instance;
