  /**
   * 
   *  false
   *  conn.setPresence() 
   * @type {Boolean}
   */
  isAutoLogin: true,

  /**
   * 
   *  true consolelog
   * @type {Boolean}
   */
  isDebug: process.env.VUE_APP_EASEMOB_ISDEBUG !== "false",

  /**
   * 
   * @type {Boolean}
   */
  enableLocalStorage: true,

  /**
   * 
   * @type {Number}
   */
  autoReconnectNumMax: 2,

  /**
   * 
   * @type {Number}
   */
  autoReconnectInterval: 2

